.rlc-calendar {
  width: 280px;
  border: 1px solid #e5e5e5;
  box-shadow: 0 0 6px -4px rgba(0, 0, 0, 0.75);
  color: #565656;
}

.rlc-days,
.rlc-days-label {
  width: 100%;
}

.rlc-days-label {
  text-transform: uppercase;
  font-size: 10px;
}

.rlc-days {
  font-size: 12px;
}

.rlc-day,
.rlc-day-label {
  display: inline-flex;
  justify-content: space-around;
  align-items: center;
  width: 40px;
  height: 40px;
  text-align: center;
  box-sizing: border-box;
}

.rlc-day {
  cursor: pointer;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.rlc-day-today {
  color: #fff;
  background-color: $grn_lht;
  border-radius: 50%;
}

.rlc-day-default:hover {
  background-color: #f3f3f3;
  color: #868686;
  border-radius: 50%;
}

.rlc-day-out-of-month {
  color: #d2d2d2;
}

.rlc-day-end-selection,
.rlc-day-inside-selection,
.rlc-day-selected,
.rlc-day-start-selection {
  color: #fff;
  background-color: $grn_drk;
}

.rlc-day-end-selection:hover,
.rlc-day-inside-selection:hover,
.rlc-day-selected:hover,
.rlc-day-start-selection:hover {
  background-color: $grn_lht;
}

.rlc-day-selected {
  border-radius: 50%;
}

.rlc-day-start-selection {
  border-radius: 50% 0 0 50%;
}

.rlc-day-end-selection {
  border-radius: 0 50% 50% 0;
}

.rlc-day-inside-selection {
  border-radius: 0;
}

.rlc-day-disabled {
  border-radius: 0;
  cursor: default;
  color: #cecece;
}

.rlc-day-disabled,
.rlc-day-disabled:hover {
  background-color: #f5f5f5;
}

.rlc-month-and-year-wrapper {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
  border-bottom: 1px solid #e5e5e5;
  border-top: 1px solid #e5e5e5;
  padding: 10px 0;
}

.rlc-month-and-year {
  flex-grow: 2;
  text-align: center;
  text-transform: uppercase;
  font-size: 14px;
}

.rlc-navigation-button-wrapper {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  justify-content: space-around;
  flex-grow: 1;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.rlc-navigation-button {
  cursor: pointer;
}

.rlc-details {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  justify-content: space-between;
}

.rlc-date-details-wrapper {
  margin: 5px 0;
}

.rlc-date-details,
.rlc-date-details-wrapper {
  flex-grow: 1;
}

.rlc-date-details {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
}

.rlc-date-number {
  font-size: 35px;
  padding: 0 5px;
}

.rlc-date-day-month-year {
  font-size: 12px;
}

.rlc-detail-day {
  margin-bottom: 2px;
}

.rlc-detail-month-year {
  text-transform: uppercase;
}

.rlc-date-time-selects {
  padding: 0 10px;
}

.rlc-separator {
  margin: 0 5px;
}

.rlc-day-marked:after {
  content: "";
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background-color: red;
  margin-top: 11.5px;
  z-index: 1;
  position: absolute;
}

.rlc-day-today.rlc-day-marked:after {
  background-color: #fff;
}
